import React, { Component } from 'react';
import classNames from 'classnames';
import { RadioGroup } from '..';

const RADIOS = [
  { label: 'Radio1', name: 'name', disabled: false },
  { label: 'Radio2', name: 'name', disabled: false },
  { label: 'Radio3', name: 'name', disabled: false },
];

const KNOBS = {
  firstStepKnobs: [
    {
      type: 'select',
      label: 'checkedRadio',
      state: 'checkedRadio',
      defaultState: 1,
      options: [],
    },
  ],
};

export default class RadioGroupDocs extends Component {
  constructor() {
    super();
    this.state = { ...this.getState() };
  }

  getState = () => {
    const state = [];
    Object.values(KNOBS).map(knob =>
      knob.map(curr => {
        state[curr.state] = curr.defaultState;
        return state;
      }),
    );
    return state;
  };

  getDocs = () => {
    const docs = Object.values(KNOBS)
      .map(knob =>
        knob
          .reduce((acc, curr) => {
            acc.push(
              `${curr.state} = {${this.state[curr.state] >= 0 ? this.state[curr.state] : ''}}`,
            );
            return acc;
          }, [])
          .join('\n\r  '),
      )
      .join();

    return (
      <pre className="tw-docs-code ">
        {`<Upload
  onChange={'console.log'}
  ${docs}
/>`}
      </pre>
    );
  };

  handleOnChange = value => {
    // eslint-disable-next-line
    console.log('Value selected', value);
  };

  render() {
    return (
      <div className="container">
        <section className="section">
          {/* Full Component */}
          <div className="row">
            <div className="col-md-6">
              <h2>Radio group</h2>
              <span role="img" aria-label="uhm">
                Radio killed the checkbox star
              </span>
              {getExtraDocs()}
              {this.getDocs()}
            </div>
            <div className="col-md-6">
              <div
                className={classNames({
                  ' m-b-4': true,
                })}
              >
                <RadioGroup
                  radios={RADIOS}
                  onChange={this.handleOnChange}
                  checkedRadio={this.state.checkedRadio}
                />
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

const getExtraDocs = () => (
  <div>
    <p className=" m-t-5">
      This component render a simple group of radio options, and broadcast the index of selected
      option <code>onChange</code>
    </p>
  </div>
);



